import { Component, OnInit, ViewEncapsulation, AfterViewInit } from '@angular/core';
import { Helpers } from '../../../../helpers';
import { ScriptLoaderService } from '../../../../_services/script-loader.service';
@Component({
    selector: 'app-themgiaovien',
    templateUrl: './themgiaovien.component.html',
    encapsulation: ViewEncapsulation.None,
})
export class ThemgiaovienComponent implements OnInit, AfterViewInit {

    constructor(private _script: ScriptLoaderService) {
    }

    ngOnInit() {
    }
    ngAfterViewInit() {
        this._script.loadScripts('app-themgiaovien',
            [
                'assets/app/js/wizard.js',
                'assets/app/js/form-repeater.js',
                'assets/app/js/dashboard.js',

            ]);
        let datatable = (<any>$('#m_datatable_latest_orders')).mDatatable({
            data: {
                type: "remote",
                source: {
                    read: {
                        url: "/api/TestApi/Teacher"
                    }
                },
                pageSize: 10,
                saveState: {
                    cookie: !1,
                    webstorage: !0
                },
                serverPaging: !0,
                serverFiltering: !0,
                serverSorting: !0
            },
            layout: {
                theme: "default",
                class: "",
                scroll: !0,
                height: 380,
                footer: !1
            },
            sortable: !0,
            filterable: !1,
            pagination: !0,
            columns: [{
                field: "RecordID",
                title: "Stt",
                sortable: !1,
                width: 40,
                selector: {
                    class: "m-checkbox--solid m-checkbox--brand"
                },
                textAlign: "center"
            }, {
                field: "name",
                title: "Tên",
                sortable: "asc",
                filterable: !1,
                width: 150
            }, {
                field: "address",
                title: "Địa chỉ",
                width: 150,
                responsive: {
                    visible: "lg"
                }
            }, {
                field: "regcourse",
                title: "Đăng ký khóa học"
            },
            {
                field: "workplace",
                title: "Nơi công tác",
                width: 100
            }, {
                field: "classes",
                title: "Danh sách lớp",
                width: 150
            }]
        });

    }
}


